import { AppBar, Button, Container, Toolbar } from '@material-ui/core'
import React,{useEffect,useState}from 'react'
import { Link } from 'react-router-dom'
import Logo from '../commons/Logo'
import SearchByName from '../commons/SearchByName'

const Header = ()=>{
    const [backgroundColor,setBackgroundColor] = useState('transparent')

   const listenScroll = ()=>{
        if (window.scrollY > 620) {
            setBackgroundColor('white')
        }else{
            setBackgroundColor('transparent')
        }
    }

    useEffect(()=>{
        window.addEventListener('scroll', listenScroll)
    },[])
    return <header>
            <AppBar style={{backgroundColor:`${backgroundColor}`}} position={'fixed'} elevation={0}>
                <Toolbar>
                    <Container maxWidth={'lg'} style={{display:"flex",flexDirection:'row'}}>
                        <Logo/>
                        <div style={{display:'flex',flexDirection:'row',flexGrow:1}}>
                         
                        </div>
                        <div style={{display:'flex',flexDirection:'row',justifyContent:'space-between'}}>
                            <Button component={Link} to={'/login'} color={'primary'} variant={'text'} style={{marginRight:50,textTransform:"none"}}>
                                Login
                            </Button>

                            <Button component={Link} to={'/signup'} elevation={0} color={'primary'} variant={'contained'} style={{textTransform:"none"}}>
                                Sign up
                            </Button>
                        </div>
                    </Container>
                </Toolbar>
            </AppBar>
    </header>
}

export default Header